@import url(https://fonts.googleapis.com/css?family=MedievalSharp&display=swap);
.chest-shell {
  margin: 10px;
}

.chest {
  background: url(/treasure-chest/static/media/chest-close.e4671554.png) no-repeat;
  background-size: contain;
  border-radius: 5px;
  width: 120px;
  height: 100px;
  padding: 10px;
  margin: 10px auto;
}

.coin-area {
  min-height: 50px;
}

.coin {
  height: 50px;
  width: 50px;
  border: 2px solid;
  border-radius: 50%;
  padding: 15px 8px;
  font-size: 14px;
  text-align: center;
  margin: 0 auto;
}

.gold-coin {
  background: gold;
  border-color: goldenrod;
}

.silver-coin {
  background: silver;
  border-color: gray;
}

.gold {
  background-image: url(/treasure-chest/static/media/gold-chest.ab9dc4d9.png);
}

.silver {
  background-image: url(/treasure-chest/static/media/silver-chest.e023663f.png);
}

.half {
  background-image: url(/treasure-chest/static/media/half-chest.65af1706.png);
}

.btn {
  display: block;
  margin: 0 auto;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  background: #edcc9f;
  font-size: 18px;

  font-family: 'MedievalSharp', cursive;
}

.app {
  width: 600px;
  margin: 20px auto;
  background: #ca9955;
  padding: 20px;
}

.header {
  text-align: center;
}

.chest-area {
  display: grid;
  grid-template-columns: 150px 150px 150px;
  grid-gap: 20px;
  margin: 0 auto;
  width: 95%;
}

